<template>
  <div>
    <v-toolbar flat color="white">
      <v-toolbar-title>Table de gestion contact</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" max-width="500px">
        <template v-slot:activator="{ on }">
          <v-btn color="primary" dark class="mb-2" v-on="on">Ajouter un contact</v-btn>
        </template>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12>
                  <v-text-field v-model="editedItem.name" label="Nom"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="editedItem.project" label="Projet"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="editedItem.length" label="Taille"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="editedItem.phone" label="Téléphone"></v-text-field>
                </v-flex>
                <v-flex xs12>
                  <v-text-field v-model="editedItem.price" label="Prix"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click="close">Annuler</v-btn>
            <v-btn color="blue darken-1" flat @click="save">Enregistrer</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table :headers="headers" :items="desserts" class="elevation-1">
      <template v-slot:items="props">
        <td>{{ props.item.name }}</td>
        <td class="text-xs-right">{{ props.item.project }}</td>
        <td class="text-xs-right">{{ props.item.length }}</td>
        <td class="text-xs-right">{{ props.item.phone }}</td>
        <td class="text-xs-right">{{ props.item.price }}</td>
        <td class="justify-center layout px-0">
          <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
          <v-icon small @click="deleteItem(props.item)">delete</v-icon>
        </td>
      </template>
    </v-data-table>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    headers: [
      {
        text: "Nom",
        align: "left",
        sortable: false,
        value: "name"
      },
      { text: "Projet", value: "project" },
      { text: "Taille", value: "length" },
      { text: "Télephone", value: "phone" },
      { text: "Prix", value: "price" },
      { text: "Actions", value: "actions" }
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      project: "",
      length: "",
      phone: "",
      price: ""
    },
    defaultItem: {
      name: "",
      project: "",
      length: "",
      phone: "",
      price: ""
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nouveau contact"
        : "Modification contact";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  async mounted() {
    await this.getContacts();
  },

  methods: {
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    async getContacts() {
      this.desserts = [];
      const data = await fetch(
        "https://cheyennetattoo-6ed46.firebaseio.com/contacts.json"
      );
      let resp = await data.json();
      Object.keys(resp).forEach(elm => {
        resp[elm]["key"] = elm;
        this.desserts.push(resp[elm]);
      });
    },

    deleteItem(item) {
      confirm("Etes vous sur de vouloir supprimer ce contact ?") &&
        fetch(
          "https://cheyennetattoo-6ed46.firebaseio.com/contacts/" +
            item.key +
            ".json",
          {
            method: "delete"
          }
        ).then(res => this.getContacts());
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    async save() {
      if (this.editedIndex > -1) {
        const data = await fetch(
          "https://cheyennetattoo-6ed46.firebaseio.com/contacts/" +
            this.editedItem.key +
            ".json",
          {
            method: "put",
            body: JSON.stringify(this.editedItem)
          }
        );
      } else {
        const data = await fetch(
          "https://cheyennetattoo-6ed46.firebaseio.com/contacts.json",
          {
            method: "post",
            body: JSON.stringify(this.editedItem)
          }
        );
      }
      this.editedItem = [];

      this.close();
      await this.getContacts();
    }
  }
};
</script>